.-b-home-main{
    padding: 0px !important;
}
.-b-home-bg1{
    background-image: url(/static/media/temp_bg.ad0a5977.jpeg);
    background-attachment: fixed;
    background-position: center;
    width: 100%;
    padding: 250px;
}
.-b-home-main-text{
    font-size: 80px;
    color: #fff; 
    text-align: left;
}
.-b-home-bg{
    background-image: url(/static/media/bg.fa25b3bf.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 750px;
}
.-b-homemain-content{
    position: absolute;
    top: 0px;
}
.-b-homemain-title{
    font-size: 5em;
    font-weight: bold;
    color: white;
    margin-top: 500px;
    margin-left: 50px;
}
.-b-homeabout-title{
    margin-top: 140px;
    font-size: 50px;
}
.-b-blockquote{
    margin-top: 80px;
}
.-b-blockquote-paragraph{
    line-height: 50px;
    font-size: 26px;
    text-align: justify;
}
.-b-homeabout-img{
    margin-top: 150px;
}
.-b-homeabout-btn{
    width: 200px;
    padding: 14px;
    font-size: 24px;
    border: 1px solid #333;
}
.-b-homewhy-paragraph{
    font-size: 30px;
    margin-top: 100px;
    padding-bottom: 140px;
}
.-b-homewhy-bg{
    background-image: url(/static/media/icon1.c7b6a364.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    height: 400px;
    opacity: 0.8;
}
.-b-blockquote-bottom{
    line-height: 40px;
    font-size: 20px;
    background-color: #ffffad;
    padding-top: 65px;
    font-weight: 500;
    padding-bottom: 50px;
    border-left: 10px solid #ffc107;
}
.-b-blockquote-bottom::before{
    font-family: Arial, Helvetica, sans-serif;
    content: "\201C" !important;
    font-size: 92px;
    position: absolute;
}
.-b-blockquote-bottom-div{
    padding-top: 60px;
    padding-bottom: 46px;
}

.-b-footer{
    background-color: #333;
    color: white;
    padding-bottom: 80px;
    padding-top: 60px;
}
.-b-footer-ul{
    list-style-type: none;
    -webkit-padding-start:0px !important;
            padding-inline-start:0px !important; 
}
.-b-footer-br{
    border-top: 1px solid #565151 !important;
}
.-b-footer-menu{
    padding-bottom: 40px;
}
.-b-main-head{
    font-family: cursive;
    font-weight: bold;
    margin-top: 60px;
    color: white;
} 
/*  */
.-b-accrodion{
    margin-top: 40px;
}
.-b-nav-panel{
    border:none;
}
.-b-nav-panel-heading{
    background-color: #05204A !important;
    color: white !important;
}
.-b-nav-panel-title > a:hover, a:focus{
    text-decoration: none !important;
}
.-b-nav-panel-title {
    font-size: 22px !important;
}
.-b-nav-panel-body{
    background-color: #05204A;
    border: none !important;
    padding: 0px !important;
}
.-b-sidenav-ul{
    -webkit-padding-start: 38px !important;
            padding-inline-start: 38px !important;
}

.-b-sidenav-ul > li {
    list-style-type: none;
}
.-b-sidenav-ul > li > a {
    text-decoration: none;
    cursor: pointer;
    font-size: 18px;
    color: white;
}
/* Top nav */
.-b-top-nav{
    background-color: #05204A;
    border-color: #05204A;
}
.-b-top-nav-ul > li > a{
    color: white !important;
    background-color: #05204A !important;
}
