.Post_form{
    width: 100%;
    padding-top: 30px;
}
.comment_button{
    text-align: right;
    padding: 20px;
}

.Post_body{
    padding: 10px;
    color: #616161;
    letter-spacing: 1.5px;
}
.Post_main{
    background-color: #f0f0f5;
    width: 100%;
    height: 100vh;
    padding-top: 30px;
}
.Post_div{
    background-color: #fff;
}
.Post_thread{
    background-color:#fff;
    padding: 15px;
    color: black;
    border: 1px solid #0000001c;
}
.query_card{
    background-color:#fff;
    padding: 15px;
    color: black;
    border: 1px solid #0000001c;
}
.Post_date{
    float: right;
    color: #616161;
    letter-spacing: 1px;
}
.Post_body{
    color: #616161;
    letter-spacing: 1px;
    display: block;
    display: -webkit-box;
    max-height: 30px;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
}
